*{
  margin: 0;
  padding: 0;
  font-family: 'poppins',sans-serif;
  text-decoration: none;
  scroll-behavior: smooth;
  
}
html{
  overflow-x: hidden;
}

body{
  background-color: #1f242d;
}
section{
  
  min-height: 100vh;
  padding: 0rem 8% 2rem;
}

/* HEADER--------------------------------------------- */
.header{
  width: 100%;
  position: fixed;
  z-index: 999;
  background-color:#1f242d;
  padding: 10px 13px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 10px 10px -2px rgba(0,0,0,0.25); 
 
}
.header.sticky{
 border-bottom: 0 2px 2px -2px rgba(0,0,0,0.25); ;
}
.logo{
  font-size: 30px; 
  margin-left: 80px;
  color: white ;
  font-weight: 100;
  text-decoration: none;
}
.span{
  color: #0ef;
}
.navbar{
  margin-right: 100px;
}
.navbar a{
  font-size: 17px;
  margin-left: 2rem;
  text-decoration: none;
  color: white;
  
}
.navbar a:hover,
.navbar a.active {
  color: #0ef;
  text-shadow: #0ef;
  text-shadow: 0 0 10px white;
}

#menu-icon{
  color: white;
  font-size: 30px;
  display: none;
}

/* BODY--------------------------------------------- */

/* HOME--------------------------------------------- */
#home{
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.home-content h3{
  color: white;
  font-weight: 700;
  font-size: 30px;
}
.home-content h3:nth-of-type(2){
  margin-bottom: 10px;
}
span{
  color: #0ef;
}
.home-content h1{
  color: white;
  font-weight: 700;
  font-size: 50px;
  line-height: 1.5;
}
.home-content p{
  color: white;
  margin-top: 5px;
}
.social-media a{
display: inline-flex;
justify-content: center;
align-items: center;
width: 32px;
height: 32px;
font-size: 32px;
border: 2px solid #0ef;
border-radius: 50%;
background: transparent;
color: #0ef;
margin: 24px 16px 24px 0;
font-size: 20px;
}
.social-media a:hover{
  background-color: #0ef;
  color: rgb(1, 8, 31);
  box-shadow: 0 0 10px solid #0ef;
}
.btn{
  display: inline-block;
  padding: 8px 20px;
  border: 2px solid #0ef;
  border-radius: 64px;
  font-size: 16px;
  font-weight: 600;
  background: transparent;
  color: #0ef;
}
.btn:hover{    
  background: #0ef;
  color: rgb(72, 72, 72);
  box-shadow: 0 0 2rem #0ef;
}
.home-img img{
  width: 600px;
  animation: floatImage 4s ease-in-out infinite;
}
@keyframes floatImage{
  0%{
      transform: translateY();
  }
  50%{
      transform: translateY(-1rem);
  }
  100%{
      transform: translateY();
  }
}


/* ABOUT ME--------------------------------------------- */
#about{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
  background: #323946;
  min-height: 100vh;
}
.about-more .heading{
  align-items: center;
}
.about-more h2{
  color: #0ef;
  font-size: 37px;
  font-weight: 750;
  line-height: 1.6;  
}
#about .about-more h2 span{
  color: rgb(255, 255, 255);
}
.about-more h3{
  font-weight: 600;
  color: white;
  font-size: 25px;    
}
.about-more p{
  color: white;
  font-size: 20px;
  margin: 16px 0 32px ;
}
.btn1{
  display: inline-block;
  background: #0ef;
  color: rgb(72, 72, 72);
  box-shadow: 0 0 20px #0ef;
  padding: 8px 20px;
  border: 2px solid #0ef;
  border-radius: 64px;
  font-size: 16px;
  font-weight: 600;   
}
.btn1:hover{
  border: 2px solid #0ef;
  background: transparent;
  color: #0ef;
  box-shadow: none;
}
.about-img img{
  width: 600px;
}


/* SERVICES--------------------------------------------- */
#service{ 
  min-height: 100vh;
  margin: 10px ;
  padding: 90px 90px ;  
}
#service .heading{
  text-align: center;
  margin-bottom: 30px;
}
#service h2{
  color: white;
  font-size: 45px;
  font-weight: 850;
  line-height: 1.6;
}
#service span{
  color: #0ef;
}
.servcontainer{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 32px;
}
.servcontainer .code{
  flex: 16 16 480px;
  border-radius: 32px;
  background: #323946;
  text-align: center;
  border: 3px solid #1f242d;
  padding: 32px 48px 48px;
  transition: 0.5 ease;
}
.servcontainer .code:hover{
  transform: scale(1.04);
  border: solid #0ef;
}
.code a i{
  color: #0ef;   
  font-size:50px ;
}
.code h3{
  font-size: 30px;
  color: white;
  line-height: 1.8;
  font-weight: 700;
}
.code p{
  color: white;
  font-weight: 300;
  font-size: 15px;
  margin-bottom: 20px;
}


/* PORTFOLIO FOR LATEST PROJECTS--------------------------------------------- */
#portfolio{  
  background-color: #323946;
  min-height: 100vh;
  padding: 90px 90px;    
}
#portfolio .heading{
  text-align: center;
  margin-bottom: 30px;
}
#portfolio h2{
  color: #0ef;
  font-size: 45px;
  font-weight: 850;
  line-height: 1.6;
}
#portfolio span{
  color: white;
}
.portcontainer{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  align-items: center;
  gap: 40px;
}
.portcontainer .portbox{
  position: relative;
  display: flex;
  overflow: hidden;
  border-radius: 32px;
  box-shadow: 0 0 2px solid #1f242d ;
}
.portcontainer .portbox img{
  width: 100%;
  transition: .5 ease;
}
.portbox:hover img{
  transform: scale(1.1);
}
.portbox .portlayer{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0,0,0,0.1),#0ef);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  transform: translateY(100%);
  transition: .5 ease;
}
.portbox:hover .portlayer{
  transform: translateY(0);
}
.portlayer h3{
  font-size: 30px;
  color: white;
}
.portlayer p{
  color: white;
  text-align: center;
  margin: 10px 30px;
}
.portlayer a{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: white;
}
.portlayer a i{
  font-size:20px ;
  color: black;
}


/* CONTACT US--------------------------------------------- */
#contact{
  min-height: 100vh;
  padding: 90px 90px;
}
#contact .heading{
  text-align: center;
  margin-bottom: 30px;
}
#contact h2{
  color: white;
  font-size: 45px;
  font-weight: 850;
  line-height: 1.6;
}
#contact span{
  color: #0ef;
}
#contact form{
 max-width: 800px;
 margin: 10px auto;
 text-align: center;
 margin-bottom: 30px;
 text-decoration: none;
}
#contact form .box{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
#contact form .box input,
#contact form .box textarea{
  width: 90%;
  padding: 18px;
  font-size: 17px;
  border-radius: 14px;
  background: #323946;
  color:white;
  margin: 10px 5px;
}
#contact form .box input{
  width: 42%;
}
#contact form .box textarea{
  resize: none;
}
.box .btn{
  font-size: 22px;
  align-items: flex-start;
  margin-top: 30px;
}


/* FOOTER--------------------------------------------- */
footer p{
 background-color: #323946;
 font-size: 20px;
 color: white;
 text-align: center;
 padding: 15px;
}
.foot p1{
  color: #0ef;
}



